<template>
  <div
    ref="wheel"
    class="wheel-select"
  >
    <div
      class="scroller"
      ref="scroller"
      @scroll="settleScroll">
      <div
        :style="{
          'padding-top': `${padding}px`,
          'padding-bottom': `${padding}px`
        }">
          <div
          v-for="option in options"
          :key="option"
          :style="{'height': `${optionHeight}px`}"
          :class="{'selected': option === selectedItem}"
          class="select-item"
          @click="selectedItem = option"
        >
          {{ getLabel(option) }}
        </div>
      </div>
    </div>
    <div
      class="overlay">
      <div
        :style="{'height': `${optionHeight}px`}"
        class="center-bounds"/>
    </div>
  </div>
</template>
<script>

export default {
  props: {
    value: {
      type: [String, Number],
      required: true,
    },
    options: {
      type: Array,
      required: true,
    },
    optionHeight: {
      type: Number,
      default: 48,
    },
    getLabel: {
      type: Function,
      default: key => key,
    },
  },

  data() {
    return {
      scrollTimeout: null,
      wheelHeight: 0,
      padding: 0,
      currentScrolledValue: null,
    };
  },

  watch: {
    value(value) {
      if (value === this.currentScrolledValue) return;
      this.scrollToValue(value);
    },
  },

  mounted() {
    this.wheelHeight = this.$refs.wheel.clientHeight;
    this.padding = (this.wheelHeight - this.optionHeight) / 2;
    this.scrollToValue(this.value);
    this.$nextTick(() => {
      this.$refs.scroller.classList.add('smooth-scroll');
    });
  },

  computed: {
    selectedItem: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('input', value);
      },
    },
  },

  methods: {
    settleScroll() {
      if (this.scrollTimeout) clearTimeout(this.scrollTimeout);
      this.scrollTimeout = setTimeout(() => this.setValue(), 200);
    },
    setValue() {
      if (!this.$refs.wheel) return;
      const { scrollTop } = this.$refs.scroller;
      const middleScrollPos = (scrollTop + (this.wheelHeight / 2)) - this.padding;
      const idx = (middleScrollPos - (middleScrollPos % this.optionHeight)) / this.optionHeight;
      this.$emit('input', this.options[idx]);
      this.scrollToValue(this.options[idx]);
    },

    scrollToValue(value) {
      if (!this.$refs.scroller) return;
      this.currentScrolledValue = value;
      const selectedIdx = this.options.indexOf(value);
      this.$refs.scroller.scrollTop = selectedIdx * this.optionHeight;
    },
  },
};
</script>
<style lang="scss" scoped>

.wheel-select {
  position: relative;

  -webkit-overflow-scrolling: touch;

  background: white;

  height: 100px;

  border: 1px solid #ced4da;
  border-radius: .25rem;


  .scroller {
    height: 100%;
    overflow-y: scroll;

    // Removing the scrollbars
    &::-webkit-scrollbar {
      width: 0 !important;
    }
    overflow-y: -moz-scrollbars-none;
    -ms-overflow-style: none;

  }


  .select-item {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 0 8px;

    transition: background-color 0.5s ease;
    &:hover:not(.selected) {
      background-color: #f8f8f8;
    }

    &.selected {
      background-color: #efefef;
    }

  }

  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;

    pointer-events: none;

    .center-bounds {
      border-bottom: 1px solid #ced4da;
      border-top: 1px solid #ced4da;
      width: 100%;
    }
  }
}

//Adding a smooth scroll effect for when the component recenters the selected item after scroll
.smooth-scroll {
  scroll-behavior: smooth;
}
</style>
